import {pageConfigs, dsConfigs} from "./PageConfigs.js";

var apis = {};
function sendRequest(path, data, noNeedRoot) {
  return new Promise(function(resolve, reject) {
    if(true !== noNeedRoot) {
      path = serviceRootPath+path;
    }
    $.post(path, data, function(res){
      resolve(res);
    }, 'json');
  })

}
//校验手机号是否被注册过
apis.phoneRegisted = async function (phone) {
  let path = "register?_cnd=exists&_method=GET&_pageSize=20&_pageNo=1";
  let data = {
    "$phone": phone
  };
  return await sendRequest(path, data);
}
//新用户注册
apis.insertUser = async function (paramObj) {
  let path = "register?_method=POST";
  let data = {
    "$user": JSON.stringify(paramObj)
  };
  return await sendRequest(path, data);
}



apis.getPageConfig = function(pageName) {
  return pageConfigs[pageName];
}
apis.getDSConfig = function(dsName) {
  return dsConfigs[dsName];
}

export default apis



