const pageConfigs = {
    "example": {
        "fieldMeta": [{
            name: "id",
            label: "主键",
            "xType": "q-input",
            "showInTable": false,
            "showInForm": false
        },{
            name: "name",
            label: "名称",
            "xType": "q-input"
        },{
            name: "startTime",
            label: "生效时间",
            "xType": "q-date"
        },{
            name: "appId",
            label: "所属应用",
            "xType": "q-select",
            "bind": ""
        }]
    },
    "roleMgr": {
        table: {
            "bind": "roleMgr",
            "fieldMeta": [{
                name: "id",
                label: "主键",
                "xType": "q-input",
                "showInTable": false,
                "showInForm": false
            },{
                name: "name",
                label: "名称",
                "xType": "q-input"
            },{
                name: "appId",
                label: "所属应用",
                "xType": "q-select",
                "bind": "appMgr"
            }]
        }

    }
}

const dsConfigs = {
    "example": {
        queryUrl: '/rest/configServer/access/roleMgr',
        createUrl: '',
        updateUrl: '',
        deleteUrl: '',
        name: 'roleEntity',
        alias: 'role'
    },
    "roleMgr": {
        url: '/rest/configServer/access/roleMgr',
        name: 'roleEntity',
        alias: 'role'
    },
    "appMgr": {
        url: '/rest/configServer/access/appMgr',
        name: 'appEntity',
        alias: 'app'
    },
    "pagePermissionMgr": {
        url: '/rest/configServer/access/pagePermissionMgr',
        name: 'pagePermissionEntity',
        alias: 'pagePermission'
    },
    "roleRelPagePermissionMgr": {
        url: '/rest/configServer/access/roleRelPagePermissionMgr',
        name: 'roleRelPagePermissionEntity',
        alias: 'roleRelPagePermission'
    },
    "apiPermissionMgr": {
        url: '/rest/configServer/access/apiPermissionMgr',
        name: 'apiPermissionEntity',
        alias: 'apiPermission'
    },
    "roleRelApiPermissionMgr": {
        url: '/rest/configServer/access/roleRelApiPermissionMgr',
        name: 'roleRelApiPermissionEntity',
        alias: 'roleRelApiPermission'
    }
}

export  {pageConfigs, dsConfigs};



