<template>
  <Layout activeMenu="roleMgr">

      <table-page v-if="showTable==0" pageName="roleMgr"
                  v-on:itemSelectEvent="itemSelectEvent">
        <Button slot="toolbar" type="info" @click="openPageSet">菜单配置</Button>
        <Button slot="toolbar" type="info" @click="openApiSet" style="margin-left: 5px">API配置</Button>
      </table-page>

      <biz-transfer v-if="showTable==1"
                    :closeTransfer.sync="showTable"
                    :config="transferConfig"></biz-transfer>

  </Layout>
</template>

<script>
  import tablePage from "../../components/TablePage.vue"
  import bizTransfer from "../../components/BizTransfer.vue"
  export default {
    components: {
      tablePage,bizTransfer
    },
    data: function() {
      return {
        showTable: 0,
        selectedItems: [],
        transferConfig: {}
      }
    },
    methods: {
      itemSelectEvent: function(selection) {
        this.selectedItems = selection;
      },
      _judgeSelected: function(){
        let res = true;
        let len = this.selectedItems.length;
        if(0==len) {
          this.$Modal.warning({content: "请选中一项！"});
          res = false;
        }
        return res;
      },
      openPageSet: function(param){
        if(this._judgeSelected()){
          this.transferConfig = {
            "left": {
              "ds": "pagePermissionMgr",
              "valueKey": "id",
              "textKey": "path"
            },
            "right": {
              "ds": "roleRelPagePermissionMgr",
              "filterKey": "roleId",
              "filterValue": this.selectedItems[0].id,
              "valueKey": "pageId"
            }
          };
          this.showTable = 1;
        };
      },
      openApiSet: function(){
        if(this._judgeSelected()){
//          this.transferConfig = {
//            "left": {
//              "ds": "apiPermissionMgr",
//              "valueKey": "id",
//              "textKey": function(item) {
//                return item.bundle+"/"+item.service;
//              }
//            },
//            "right": {
//              "ds": "roleRelApiPermissionMgr",
//              "filterKey": "roleId",
//              "filterValue": this.selectedItems[0].id,
//              "valueKey": "apiPermissionId"
//            }
//          };

          this.showTable = 2;
        }

      }
    }
  }
</script>

<style>
</style>



