<template>
  <div class="biz-transfer">
    <div class="ivu-card-head" style="margin-bottom: 15px">
      <p>权限配置</p>
    </div>
    <Transfer
            :data="data"
            :target-keys="targetKeys"
            @on-change="handleChange"></Transfer>

    <div style="margin-top: 15px">
      <i-button type="text" @click="cancel">取消</i-button>
      <i-button type="primary" @click="handleSave">保存</i-button>
    </div>
  </div>
</template>

<script>
  import {DataSource} from "../utils/dataSource.js";
  import DataCenter from "../store/DataCenter.js";


  export default {
    props: ['closeTransfer', "config"],
    data: function() {
      return {
        data: [],
        targetKeys: []
      }
    },
    mounted: function() {
        this._refreshListData(this.config);
    },
    methods: {
      _refreshListData: async function(config){
        let leftConfig = config.left;
        this.leftDS = new DataSource(DataCenter.getDSConfig(leftConfig.ds));
        let response = await this.leftDS.paged();
        let data = await response.json();
        if(data.success) {
          //取出Id
          let dataArr = [];
          for(let i= 0,len=data.result.length; i<len; i++) {
            let item = data.result[i];
            let textKey = leftConfig.textKey;
            let text;
            if('string'==typeof textKey) {
              text = item[textKey];
            }else {
              text = textKey(item); //渲染函数
            }
            dataArr.push({
              "key": item[leftConfig.valueKey],
              "label": text
            });
          }
          this.data = dataArr;
        }

        let rightConfig = config.right;
        this.rightDS = new DataSource(DataCenter.getDSConfig(rightConfig.ds));
        let response2 = await this.rightDS.paged({[rightConfig.filterKey]: rightConfig.filterValue});
        let data2 = await response2.json();
        if(data2.success) {
          //取出Id
          let targetArr = [];
          for(let j= 0,len=data2.result.length; j<len; j++) {
            let item2 = data2.result[j];
            targetArr.push(item2[rightConfig.valueKey]);
          }
          this.targetKeys = targetArr;
        }
      },
      handleChange: function(newTargetKeys) {
        this.targetKeys = newTargetKeys;
      },
      cancel: function(){
        this.$emit('update:closeTransfer', 0);
      },
      handleSave: function(){

      }
    }
  }
</script>

<style>
  .biz-transfer .ivu-transfer .ivu-transfer-list{
    height: 400px;
  }
</style>




